section{z-index:1;display:flex;flex-direction:column;gap:var(--margin-row);width:100%;padding:var(--margin-row) var(--margin);background:var(--background-color)}section#intro{display:grid;grid-template-columns:3.5fr 4fr .5fr;gap:2rem 1rem;padding-bottom:calc(var(--margin-row)*2)}@media screen and (max-width: 1440px){section#intro{grid-template-columns:repeat(2, 1fr)}}@media screen and (max-width: 768px){section#intro{grid-template-columns:100%}}section#intro h2{font-weight:var(--font-weight)}section#bio{padding-top:calc(var(--margin-row)*2)}section#bio .-rw{left:calc(var(--margin)*-1);width:100vw}section#info{display:flex;flex-direction:column}section#map{display:flex;flex-direction:column}section#map img{left:calc(var(--margin)*-1);width:100vw}section#footer{position:sticky;z-index:0;display:flex;flex-direction:column;align-items:center;justify-content:center;gap:var(--margin-row);top:0;left:0;bottom:0;height:100vh;color:var(--background-color);background:var(--foreground-color)}section#footer span{color:var(--background-color)}section#footer span.title{font-size:var(--display-size);font-weight:var(--display-weight);line-height:1;letter-spacing:var(--display-spacing)}section#footer span.title span{font-family:LibreCaslon,InterVariable,PretendardVariable,-system-ui,system-ui,sans-serif;font-weight:600;letter-spacing:-0.0375em}section#footer span.copyright{margin-top:1rem;margin-bottom:var(--margin-row);font-family:LibreCaslon,InterVariable,PretendardVariable,-system-ui,system-ui,sans-serif;font-weight:500;letter-spacing:.05em}section#footer .text{font-family:LibreCaslon,InterVariable,PretendardVariable,-system-ui,system-ui,sans-serif;font-weight:500;letter-spacing:.05em}