.calendar{display:grid;grid-template-columns:repeat(7, 1fr);align-items:center;width:100%;max-width:704px;margin:0 auto}.calendar div{grid-column:1/-1;display:grid;grid-template-columns:repeat(7, 1fr)}.calendar div.date span{margin-top:2rem}.calendar div.date span:nth-child(7n),.calendar div.date span:nth-child(7n+1){color:var(--pink-color)}.calendar div.date span.prev,.calendar div.date span.next{color:var(--foreground-color);opacity:.375}.calendar div.date span.pres.-s11{color:var(--foreground-color)}.calendar div.date span.pres.-s11::after{content:"";position:absolute;z-index:-1;display:block;top:50%;left:50%;width:2.5rem;height:2.5rem;background:rgba(var(--foreground-rgb), 0.25);border-radius:.25rem;transform:translate3d(-50%, -50%, 0)}.calendar span{display:block;font-size:var(--label-size);font-weight:var(--label-weight);letter-spacing:var(--label-spacing);line-height:var(--label-height);text-align:center}