section#hero{display:flex;align-items:center;justify-content:center;height:100vh}section#hero .text{position:fixed;z-index:2;left:50%;width:15rem;font-family:LibreCaslon,InterVariable,PretendardVariable,-system-ui,system-ui,sans-serif;text-align:center;will-change:transform,opacity;pointer-events:none}section#hero .text:nth-of-type(1){top:2rem}section#hero .text:nth-of-type(2){bottom:2rem}section#hero .title{display:flex;align-items:flex-start;height:1em;font-size:var(--display-size);font-weight:var(--font-weight);line-height:1;letter-spacing:var(--display-spacing);will-change:transform,opacity}section#hero .title .shawn{font-family:LibreCaslon,InterVariable,PretendardVariable,-system-ui,system-ui,sans-serif;font-weight:600;letter-spacing:-0.01em}section#hero .title .glyph{display:flex;flex-direction:column;align-items:center;height:1em;overflow:hidden}section#hero .title .glyph :last-child{color:var(--red-color)}section#hero ul{position:absolute;z-index:-1;display:flex;align-items:center;top:0;left:0;width:100%;height:100%;-webkit-mask-image:linear-gradient(to right, rgba(0, 0, 0, 0) 0%, rgb(0, 0, 0) 16%, rgb(0, 0, 0) 84%, rgba(0, 0, 0, 0) 100%);mask-image:linear-gradient(to right, rgba(0, 0, 0, 0) 0%, rgb(0, 0, 0) 16%, rgb(0, 0, 0) 84%, rgba(0, 0, 0, 0) 100%);overflow:hidden}section#hero ul li{display:flex;align-items:center;width:86.4rem;will-change:transform}section#hero ul li div{position:absolute;flex-shrink:0}section#hero ul li div:nth-of-type(1){left:5rem;width:18rem;height:24rem}section#hero ul li div:nth-of-type(2){top:4rem;left:29rem;width:15rem;height:20rem}section#hero ul li div:nth-of-type(3){bottom:4rem;left:43rem;width:15rem;height:20rem}section#hero ul li div:nth-of-type(4){top:11rem;left:64rem;width:12rem;height:16rem}section#hero ul li div:nth-of-type(5){bottom:7rem;left:74.4rem;width:12rem;height:16rem}section#hero ul li div img{width:100%;height:100%;-o-object-position:center center;object-position:center center;-o-object-fit:cover;object-fit:cover;image-rendering:auto;will-change:transform,opacity;opacity:0;animation:scaleUp 1s ease .8s forwards;filter:saturate(30%) brightness(80%)}@keyframes scaleUp{from{transform:scale(0.8);opacity:0}to{transform:scale(1);opacity:1}}