section#cover{padding:unset;padding-top:56.25%;overflow:hidden}section#cover ul{position:absolute;z-index:1;display:flex;align-items:center;top:1rem;left:0}section#cover ul span{z-index:1;color:var(--background-color);font-size:10vw;font-weight:var(--display-weight);line-height:1;letter-spacing:var(--display-spacing)}section#cover img{position:absolute;display:flex;flex-direction:column;top:-21.875%;-o-object-position:center center;object-position:center center;-o-object-fit:cover;object-fit:cover}